import $ from 'jquery';

const section = '.js-module-compare',
      selectorBar = '.module-compare__compare',
      targetSelector = '.module-compare__wrapper ';

function init() {


    $(section).on('click',selectorBar, function(e) {
        e.preventDefault();

        $(this).toggleClass('active');
        $(this).closest(section).find(targetSelector).slideToggle('slow');
    });
}

init();

